"use client";

import { useState, useEffect, useRef } from "react";
import { useMutation, useQuery } from "convex/react";
import { api } from "../../../convex/_generated/api";
import { Id } from "../../../convex/_generated/dataModel";
import { MessageList } from "@/components/chat/MessageList";

export function ChatPanel({
  onSessionCreated,
}: {
  onSessionCreated?: (sessionId: Id<"sessions">) => void;
}) {
  const [input, setInput] = useState("");
  const [sessionId, setSessionId] = useState<Id<"sessions"> | null>(null);
  const [sending, setSending] = useState(false);
  const createdRef = useRef(false);

  const createDemo = useMutation(api.sessions.createDemo);
  const sendMessage = useMutation(api.messages.send);
  const generateResponse = useMutation(api.ai.generateResponse);
  const messages = useQuery(
    api.messages.list,
    sessionId ? { sessionId } : "skip",
  );

  useEffect(() => {
    if (createdRef.current) return;
    createdRef.current = true;
    createDemo().then((id) => {
      setSessionId(id);
      onSessionCreated?.(id);
    });
  }, [createDemo, onSessionCreated]);

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    const trimmed = input.trim();
    if (!trimmed || !sessionId || sending) return;

    setSending(true);
    setInput("");
    try {
      await sendMessage({ sessionId, role: "user", content: trimmed });
      await generateResponse({ sessionId, userMessage: trimmed });
    } finally {
      setSending(false);
    }
  };

  const canSend = !!sessionId && input.trim().length > 0 && !sending;

  if (!sessionId) {
    return (
      <div className="flex h-full flex-col items-center justify-center bg-white dark:bg-zinc-900">
        <p className="text-sm text-zinc-400">Starting session...</p>
      </div>
    );
  }

  return (
    <div className="flex h-full flex-col bg-white dark:bg-zinc-900">
      <MessageList messages={messages ?? []} />
      <form
        onSubmit={handleSubmit}
        className="border-t border-zinc-200 p-3 dark:border-zinc-700"
      >
        <div className="flex gap-2">
          <input
            type="text"
            value={input}
            onChange={(e) => setInput(e.target.value)}
            placeholder="Describe what you'd like to change..."
            disabled={sending}
            className="flex-1 rounded-lg border border-zinc-300 bg-zinc-50 px-3 py-2 text-sm outline-none placeholder:text-zinc-400 focus:border-zinc-500 disabled:opacity-50 dark:border-zinc-600 dark:bg-zinc-800 dark:text-white dark:placeholder:text-zinc-500 dark:focus:border-zinc-400"
          />
          <button
            type="submit"
            disabled={!canSend}
            className="rounded-lg bg-zinc-900 px-3 py-2 text-sm font-medium text-white transition-colors hover:bg-zinc-700 disabled:opacity-50 dark:bg-zinc-100 dark:text-zinc-900 dark:hover:bg-zinc-300"
          >
            <svg
              xmlns="http://www.w3.org/2000/svg"
              viewBox="0 0 20 20"
              fill="currentColor"
              className="h-4 w-4"
            >
              <path d="M3.105 2.288a.75.75 0 0 0-.826.95l1.414 4.926A1.5 1.5 0 0 0 5.135 9.25h6.115a.75.75 0 0 1 0 1.5H5.135a1.5 1.5 0 0 0-1.442 1.086l-1.414 4.926a.75.75 0 0 0 .826.95l14.095-5.638a.75.75 0 0 0 0-1.398L3.105 2.289Z" />
            </svg>
          </button>
        </div>
      </form>
    </div>
  );
}
